import axios from "axios"
import { Loading, Message } from 'element-ui';
import storage from '@/utils/storage';
import router from '../router';

axios.defaults.timeout = 10000;
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8'


let baseUrl = process.env.VUE_APP_BASEURL

let requestCount = 0    //
//loading
export function ShowLoading() {
  if (requestCount === 0) {
    Loading.service({ background: "rgba(0,0,0,0.2)" })
  }
  requestCount++
}

//loading
export function HideLoading() {
  if (requestCount <= 0) return
  requestCount--

  if (requestCount === 0) {
    Loading.service().close()
  }
}

//
axios.interceptors.request.use(
  req => {
    //token
    let users = storage.get("users");
    if (users && users.token) {
      req.headers.Authorization = users.token;
    }
    if (req.showLoading) {
      ShowLoading()
    }
    return req
  },
  err => {
    return Promise.reject(err)
  }
);
//
axios.interceptors.response.use(
  res => {
    if (res.config.showLoading) {
      HideLoading()
    }
    if (res.data.code === 200) {
      return res
    } else if (res.data.code === 401) {
      Message({
        message: '',
        type: 'error',
        duration: 2000
      })
      // token
      storage.clear();
      // 
      router.replace({
        path: 'login',
        query: { redirect: router.currentRoute.fullPath }
      })
    } else if (res.data.code === 403) {
      Message({
        message: ',',
        type: 'error',
        duration: 2000
      })
      // // token
      // storage.clear();
      // // 
      // router.replace({
      //   path: 'login',
      //   query: { redirect: router.currentRoute.fullPath }
      // })
    } else {
      Message({
        message: res.data.msg,
        type: 'error',
        duration: 2000
      })
    }

  },
  err => {
    //console.log(err.response.status)
    Loading.service().close();
    if (err.response.status == 504 || err.response.status == 404) {
      Message.error({ message: '' });
    } else if (err.response.status == 403) {
      Message.error({ message: ',!' });
    } else {
      Message.error({ message: '' });
    }
    return Promise.reject(err);
  }

)

/*
**  ShowLoading requestCount + 1
**  HideLoadingrequestCount - 1
**  requestCount 0  loading
*/

//postloadingshowLoadingfalse
// axios.defaults.headers.post['responseType'] = 'blob'
export function post(url, params = {}, loading = { showLoading: true }) {
  return new Promise((resolve, reject) => {
    axios.post(baseUrl + url, params, loading)
      .then(res => {
        resolve(res.data)
      }).catch(err => {
        reject(err)
      })
  })
}
//getloadingshowLoadingfalse
export function get(url, params = {}, loading = { showLoading: true }) {
  return new Promise((resolve, reject) => {
    axios.get(baseUrl + url, {
      params: params
    }, loading)
      .then(res => {
        resolve(res.data)
      })
      .catch(err => {
        reject(err)
      })
  })
}

